body {
  width: 100%;
  margin: 0;
  font-family: sans-serif;
  color: white;
  background-color: #2c2c2c;
}

.container {
  width: 100vw;
  min-height: 100vh;
  background-color: #2c2c2c;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

p {
  margin: 0;
  text-align: center;
}

.header p {
  font-size: 1.2rem;
  font-weight: 650;
  margin-bottom: 2rem;
}

.digit-container {
  display: flex;
  justify-content: center;
}

.primary_digit {
  font-size: 4rem;
}

.sec_digit {
  font-size: 2.5rem;
  margin-top: 1.5rem;
}

.txt {
  text-align: center;
}

.digit-container div {
  margin: 1rem 0.4rem;
}

.btn-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: -0.8rem;
}

.btn-container div {
  margin: 1rem 1.5rem;
  font-size: 1.6rem;
}

#pause {
  display: none;
}

#start,
#pause {
  font-size: 3rem;
  margin-left: 1rem;
}

.log-container {
  visibility: hidden;
  height: 30vh;
}

.log-container p {
  font-size: 1.2rem;
  font-weight: 650;
  margin: 1rem 0;
}

.table-container {
  width: 70vw;
  max-width: 200px;
  max-height: 30vh;
  overflow-y: scroll;
  display: flex;
  justify-content: center;
  font-size: 1.2rem;
}

.table-container::-webkit-scrollbar {
  width: 10px;
}
.table-container:hover::-webkit-scrollbar-thumb {
  background-color: #888;
}
